import { Component, OnInit } from '@angular/core';
import { Camera, CameraResultType , CameraSource } from '@capacitor/camera';
import { Router } from '@angular/router';
import { ApicallService } from 'src/app/services/CallApi/apicall.service';
import { GlobalService } from 'src/app/services/data/global.service';


@Component({
  selector: 'app-add-plot',
  templateUrl: './add-plot.page.html',
  styleUrls: ['./add-plot.page.scss'],
})
export class AddPlotPage implements OnInit {


  public Add_Plot : any ={plot:'',plot_no:'', area_marla:'',   area_sqf:'' , p_location: ''  ,  type:'', price:'', img:'',status:'', description:'' }
  public price_marla: any ;
  public property_total_size:any ;
  public size_plot:any={total_size:''}
  public selectTabs:any = 'Baba Fareed';

  public type:any='Commercial'
  constructor(public apiCall:ApicallService, private router: Router , public global: GlobalService) { }

  ngOnInit() {
 
  }

  // Insert Plot_Data
  async Add_Plot_Data(){

    this.Add_Plot.area_sqf = this.property_total_size;
    this.Add_Plot.plot = this.selectTabs;
    console.log(this.Add_Plot.plot);
    console.log(this.Add_Plot);
    await this.apiCall.api_insert_plots(this.Add_Plot);
    this.Add_Plot = {plot:'',plot_no:'', area_marla:'',   area_sqf:'' , p_location: ''  ,  type:'', price:'', img:'',status:'', description:'' }
    this.property_total_size = '';
    this.size_plot.total_size = '';
    this.price_marla = '';
    document.getElementById('cameraImage').setAttribute('src', './../../../assets/placholder_img.png');
  }

  check(){
    console.log(this.Add_Plot)
  }


  async takePictureBase64(){
    const image = await Camera.getPhoto({
      quality:90,
      resultType: CameraResultType.Base64,
      source: CameraSource.Prompt,
      allowEditing: true
    });
    document.getElementById('cameraImage').setAttribute('src', `data:image/${image.format};base64,`+image.base64String );
      console.log(image.base64String);
      this.Add_Plot.img = image.base64String;
  }
 

  Property(){
    this.router.navigate(['property'])
  }
  Plot(){
    this.router.navigate(['plots'])
  }
  add_plot(){
    this.router.navigate(['add-plot'])
  }
  Home(){
    this.router.navigate(['home'])
  }

  price_calculations($event){
    this.price_marla = $event.target.value;
    if(this.Add_Plot.type == 'Residential'){
      const q = this.property_total_size / this.size_plot.total_size;
      this.Add_Plot.price = this.price_marla * q;
      console.log($event.target.value);
    }
    if(this.Add_Plot.type == 'Commercial'){
      const q = this.property_total_size / this.size_plot.total_size
      this.Add_Plot.price = this.price_marla * q;
      console.log($event.target.value);
    }
  }

  area_calculations(){
  
    if( this.Add_Plot.type == 'Residential' ){
      const x =( (this.Add_Plot.area_marla * this.size_plot.total_size) + this.Add_Plot.area_sqf);
      this.property_total_size = x;
      const q = this.property_total_size / this.size_plot.total_size;
      this.Add_Plot.price = this.price_marla *  q ;
      console.log(this.property_total_size)
    }
     
    if(  this.Add_Plot.type == 'Commercial' ){
      const x =( (this.Add_Plot.area_marla * this.size_plot.total_size) + this.Add_Plot.area_sqf);
      this.property_total_size = x;
      const q = this.property_total_size / this.size_plot.total_size;
      this.Add_Plot.price = this.price_marla *  q ;
      console.log(this.property_total_size)
    }
      
  }

  
}


